
import React from 'react';
import s from './Users.module.css';
import userPhoto from '../../../assets/images/images.jpg';
import {NavLink} from "react-router-dom";

const User = ({user, followingInProgress, unfollow, follow}) => {
    return (
        <div>
        <span>
            <div>
                <NavLink to={'/profile/' + user.id}>
                <img className={s.img} src={user.photos.small != null ? user.photos.small : userPhoto}/>
                </NavLink>
            </div>
            <div>
                    {user.followed
                        ? <button disabled={followingInProgress
                            .some(id => id === user.id)} className={s.button} onClick={() => {
                            unfollow(user.id)
                        }}>Unfollow</button>
                        : <button
                            disabled={followingInProgress
                                .some(id => id === user.id)}
                            className={s.button} onClick={() => {
                            follow(user.id)
                        }}>Follow</button>}
            </div>
        </span>
                    <span>
            <div className={s.status}>{user.name}</div>
            <div className={s.status}>{user.status}</div>
        </span>
                    <span>
            <div className={s.status}>{"user.location.country"}</div>
            <div className={s.status}>{"user.location.city"}</div>
        </span>
                </div>)
}
export default User;